import React, { Component } from 'react'
import clx from 'classnames'
import styles from './index.module.scss'
import PageTitle from '../../component/PageTitle'
import OrderItem from '../../component/OrderItem'
import Loader from '../../component/Loader'

class OrderList extends Component {
  componentDidMount () {
    const { getOrders } = this.props.actions
    // fetch new orders
    getOrders()
  }
  render () {
    const { totalOrders, totalPrice, isLoading } = this.props
    const isLoadingClass = { hidden: isLoading }
    return (
      <>
        <PageTitle>Your Orders</PageTitle>
        <Loader isLoading={isLoading} />
        <div
          key='orderList'
          className={clx(styles.orderList, isLoadingClass)}
        >
          {
            totalOrders.map(order => (
              <OrderItem {...order} key={order.id} />
            ))
          }
        </div>
        <div
          key='summary'
          className={clx(styles.summary, isLoadingClass)}
        >
          Total <span className={styles.totalPrice}>$ {totalPrice}</span>
        </div>
      </>
    )
  }
}

export default OrderList
