import React from 'react'
import clx from 'classnames'
import { Link } from 'react-router-dom'
import {
  MdEdit,
  MdDelete
} from 'react-icons/md'
import styles from './index.module.scss'
import { MODIFY_MAP } from '../../constants'

const OrderItem = props => {
  const { id, name, price, notes } = props
  return (
    <div className={styles.orderItem}>
      <span key='drinkName' className={styles.drinkTitle}>{name}</span>
      <span
        key='drinkPrice'
        className={clx(
          styles.drinkTitle,
          styles.drinkPrice
        )}
      >
        $ {price}
      </span>
      <p key='drinkNotes' className={styles.drinkNotes}>{notes}</p>
      <div className={styles.actionArea}>
        <Link className={styles.actionLink} to={`/update/${id}`}>
          <MdEdit className={styles.actionIcon} /> {MODIFY_MAP.update}
        </Link>
        <Link className={styles.actionLink} to={`/delete/${id}`}>
          <MdDelete className={styles.actionIcon} /> {MODIFY_MAP.delete}
        </Link>
      </div>
    </div>
  )
}

export default OrderItem
